import React from "react";
import {useDispatch, useSelector} from "react-redux";
import {Link} from "react-router-dom";
import darkLogo from "../../assets/img/logo-dark.png";
import logo from "../../assets/img/logo.png";
import {openModal} from "../../Actions/ModalAction";


function HeaderLeft({isOpen, handlerOpenMenu}) {
    const sessionTheme = useSelector(state => state.User.sessionTheme);
    const isAuth = useSelector(state => state.User.isAuth);
    const dispatch = useDispatch();


    return (
        <div className="header__left">
            <Link to="/" className="header__logo">
                {
                    !sessionTheme ?
                        <img src={darkLogo} alt="logo"/>
                        :
                        <img src={logo} alt="logo"/>
                }
            </Link>
            {
                isAuth ?
                    window.innerWidth < 1023 ?
                        (!isOpen ?
                            <a className="menu-open"
                               onClick={handlerOpenMenu}
                               style={{display: `${isOpen ? "" : "flex"}`}}
                            >
                                <i className="ic-burger"></i>
                            </a>
                            :
                            <a className="menu-close"
                               onClick={handlerOpenMenu}
                               style={{display: `${!isOpen ? "" : "flex"}`}}
                            >
                                <i className="ic-close"></i>
                            </a>)
                        : ""
                    :
                    <a className="button chat__bottom-button btn-registration mobile-show"
                       data-izimodal-open="#auth"
                       onClick={() => dispatch(openModal("Auth"))}
                       data-izimodal-transitionin="fadeInDown">
                        ВОЙТИ
                    </a>
            }
        </div>
    )
}

export {
    HeaderLeft
}