import defImg from "../assets/img/fox.png";
import {CHANGE_SESSION_THEME} from "../Actions/UserAction";

const UserReducer = (state = {
    isAuth: true,
    sessionTheme: false,
    role_id: 0,
    balance: 3001,
    countOfCoinflipGame: 30,
    avatar: defImg,
    lvl: 4,
    id: 0
}, action) => {
    switch (action.type) {
        case CHANGE_SESSION_THEME: return {
            ...state,
            sessionTheme: !state.sessionTheme
        }
        default: return state
    }
};


export {
    UserReducer
}