import {Link} from "react-router-dom";
import React from "react";
import {SocialAdaptive, SocialBlock} from "./UI/SocialBlock";
import {NavBlock} from "./UI/NavBlock";

function HeaderCenter({isOpen, nowIs}){
    return (
        <div className={`header__center ${isOpen ? "header__center--active" : ""}`}>
            <NavBlock nowIs={nowIs}/>
            <SocialBlock/>
            <SocialAdaptive/>
        </div>
    )
};


export {
    HeaderCenter
}