import React from "react";
import {useDispatch} from "react-redux";
import {closeModal, openModal} from "../../../Actions/ModalAction";


const CreateGame = () => {
    const dispatch = useDispatch();
    return (
        <div className="modal modal--bet" id="createGame">
            <div className="modal__top">
                <h6 className="modal__title">Coinflip</h6>
                <a className="modal__close" data-izimodal-close onClick={() => dispatch(closeModal)}><i
                    className="ic-close"></i></a>
            </div>
            <div className="modal__body">
                <div className="conclusion__balance">
                    <p className="conclusion__balance-text">Round hash:</p>
                    <a href="" target="blank" className="conclusion__balance-hash"></a>
                </div>
                <div className="coin-create">
                    <div className="coin-create__orange">
                        <a onClick={() => {
                            dispatch(closeModal);
                            setTimeout(() =>  dispatch(openModal('BetCoinOrange')), 150)
                        }}
                            // onClick="MainFunction.selectColor('orange');"
                           data-izimodal-open="#betModalCoin"
                           data-izimodal-transitionin="fadeInDown"
                           className="coin-create__icon">
                            <i className="ic-coin-orange"></i>
                        </a>
                        <p className="coin-create__tickets">0-50</p>
                    </div>
                    <div className="coin-create__center coin-create__center--one">
                        <span className="coin-create__vs">vs</span>
                        <p className="wait-palyer__bet">Bank</p>
                        <p className="wait-palyer__bet-done">0</p>
                        <div className="wait-palyer__timer hide">
                            <i className="ic-timer"></i>
                            <p className="wait-palyer__timer-text">0:<span id="coinModalTimer"
                                                                           className="wait-palyer__timer-value">10</span>
                            </p>
                        </div>
                    </div>
                    <div className="coin-create__blue">
                        <a onClick={() => {
                            dispatch(closeModal);
                            setTimeout(() => dispatch(openModal('BetCoinBlue')), 150)
                        }}
                            // onClick="MainFunction.selectColor('blue');"
                           data-izimodal-open="#betModalCoin"
                           data-izimodal-transitionin="fadeInDown"
                           className="coin-create__icon">
                            <i className="ic-coin-blue"></i>
                        </a>
                        <p className="coin-create__tickets">50-100</p>
                    </div>
                </div>
                <div className="modal-hint">
                    <p className="modal-hint__text">Выберите цвет</p>
                </div>
            </div>
        </div>
    )
}


export {
    CreateGame
}