import React, {useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {changeTheme} from "../Help/ChangeTheme";
import {changeSessionTheme} from "../../../Actions/UserAction";


const SocialBlock = () => {
    const sessionTheme = useSelector(state => state.User.sessionTheme);
    const dispatch = useDispatch();
    return(
        <div className="social-menu__wrap">
            <ul className="social-menu">
                <li className="social-menu__item">
                    <a href="https://t.me/fox_x_ru" className="social-menu__link" title="telegram">
                        <i className="ic-telegram"></i>
                    </a>
                </li>
                <li className="social-menu__item" title="vk">
                    <a href="https://vk.com/foxxru" className="social-menu__link">
                        <i className="ic-vk"></i>
                    </a>
                </li>
                <li className="social-menu__item" title="vk-chat">
                    <a href="https://vk.com/write-177183494" className="social-menu__link">
                        <i className="ic-vk-blue"></i>
                    </a>
                </li>
                <li className="social-menu__item">
                    <a href="#javascript:void(0);"
                       onClick={()=>{
                           changeTheme(sessionTheme);
                           dispatch(changeSessionTheme)
                       }}
                        // onClick="MainFunction.activateTheme(this);"
                       data-current-theme="{{ session('theme') ? session('theme') : 'light' }}"
                       className="social-menu__link">
                        {!sessionTheme?
                            <i className="ic-sun">
                            </i>
                            :
                            <i className="ic-moon">
                            </i>
                        }
                    </a>
                </li>
                <li className="social-menu__item">
                    <a className="social-menu__link">
                        <i className="ic-user"></i>
                        <span></span>
                    </a>
                </li>
            </ul>
        </div>
    )
}
const SocialAdaptive = () => {
    const sessionTheme = useSelector(state => state.User.sessionTheme);
    const [isOpen, setIsOpen] = useState(false);
    const dispatch = useDispatch();

    return(
        <div className="social-menu__wrap--adaptive">
            <a className="social-menu__open" onClick={()=>setIsOpen(!isOpen)} style={{display:`${!isOpen?"inline-block":"none"}`}}>
                <span></span>
            </a>
            <a className="social-menu__close" onClick={()=>setIsOpen(!isOpen)} style={{display:`${isOpen?"inline-block":"none"}`}}>
                <i className="ic-close"></i>
            </a>
            <ul className="social-menu" style={{display:`${!isOpen?"none":"flex"}`,backgroundColor:`${!sessionTheme?"#181a1e" : "#ededed"}`}}>
                <li className="social-menu__item">
                    <a href="https://t.me/fox_x_ru" className="social-menu__link" title="telegram">
                        <i className="ic-telegram"></i>
                    </a>
                </li>
                <li className="social-menu__item" title="vk">
                    <a href="https://vk.com/foxxru" className="social-menu__link">
                        <i className="ic-vk"></i>
                    </a>
                </li>
                <li className="social-menu__item" title="vk-chat">
                    <a href="https://vk.com/write-177183494" className="social-menu__link">
                        <i className="ic-vk-blue"></i>
                    </a>
                </li>
                <li className="social-menu__item">
                    <a href="#javascript:void(0);"
                       onClick={()=>{
                           changeTheme(sessionTheme);
                           dispatch(changeSessionTheme)
                       }}
                        // onClick="MainFunction.activateTheme(this);"
                       data-current-theme="{{ session('theme') ? session('theme') : 'light' }}"
                       className="social-menu__link">
                        {!sessionTheme?
                            <i className="ic-sun">
                            </i>
                            :
                            <i className="ic-moon">
                            </i>
                        }
                    </a>
                </li>
                <li className="social-menu__item">
                    <a className="social-menu__link">
                        <i className="ic-user"></i>
                        <span></span>
                    </a>
                </li>
            </ul>
        </div>
    )
};


export {
    SocialBlock,
    SocialAdaptive
}