import React from "react";
import {MainCenterWrapper} from "../../Wrapper/MainCenterWrapper";
import {useSelector} from "react-redux";
import {HelpBlockMobile} from "../../Components/HelpBlock";


function DailyBonusCenter() {
    let lvl = useSelector(state => state.User.lvl);
    let isAvailable = true;
    return (
        <MainCenterWrapper>
            <div className="starting">
                <h2 className="starting__title">ЕЖЕДНЕВНЫЙ БОНУС</h2>
                <p className="starting__text">Заходите на сайт каждый день и получайте различные бонусы совершенно
                    бесплатно! У Вас есть шанс выиграть до 1000 coins, не упустите удачу!!!</p>
            </div>
            <div className="padding-wrap">
                {
                    lvl <= 3 ?
                        <>
                            <div className="hint">
                                <span className="hint__icon">!</span>
                                <div className="hint__wrap">
                                    <p className="hint__title">Внимание!</p>
                                    <p className="hint__text">Для открытия ежедневного бонуса достигните 3 уровня</p>
                                </div>
                            </div>
                            <div className="get-bonus">
                                <div className="get-bonus__body">
                                    <p className="disabled-bonus__button">
                                        Получить <br/>
                                        БОНУС
                                    </p>
                                </div>
                            </div>
                        </>
                        :
                        <>
                            {
                                isAvailable ?
                                    <>
                                        <GetBonus/>
                                        <BonusSlider/>
                                    </>
                                    :
                                    <BonusIsNotAvailable/>


                            }

                        </>
                }
            </div>
            <HelpBlockMobile/>
        </MainCenterWrapper>
    )
}

const BonusSlider = ({items = [0,20,50,100,200,500,1000,5000,10000]}) => {
    return (
        <div className="bonus">
            <div className="bonus__slider">
                {items.map(item => {
                    return <BonusSliderItem item={item}/>
                })}
            </div>
            <div className="bonus__point">
                <i className="ic-roulette-arrow"></i>
            </div>
        </div>
    )
};
const BonusSliderItem = ({item = 0}) => {
    switch (item) {
        case item === 0: return (
            <div className="bonus__slider-item bonus-item" data-count="{{ $a }}">
                <span className="bonus-item__value">{item}</span>
                <span className="bonus-item__coins">COINS</span>
            </div>
        );
        case item === 100: return (
            <div className="bonus__slider-item bonus-item bonus-item--orange" data-count="{{ $a }}">
                <span className="bonus-item__value">{item}</span>
                <span className="bonus-item__coins">COINS</span>
            </div>
        );
        case item === 20 || item === 50: return (
            <div className="bonus__slider-item bonus-item bonus-item--lose" data-count="{{ $a }}">
                <span className="bonus-item__value">{item}</span>
                <span className="bonus-item__coins">COINS</span>
            </div>
        );
        case item === 200 || item === 500: return (
            <div className="bonus__slider-item bonus-item bonus-item--green" data-count="{{ $a }}">
                <span className="bonus-item__value">{item}</span>
                <span className="bonus-item__coins">COINS</span>
            </div>
        );
        case item === 1000 || item === 5000: return (
            <div className="bonus__slider-item bonus-item bonus-item--yellow" data-count="{{ $a }}">
                <span className="bonus-item__value">{item}</span>
                <span className="bonus-item__coins">COINS</span>
            </div>
        );
        default: return(
            <div className="bonus__slider-item bonus-item bonus-item--blue" data-count="{{ $a }}">
                <span className="bonus-item__value">{item}</span>
                <span className="bonus-item__coins">COINS</span>
            </div>
        )

    }
};


const GetBonus = () => {
    return(
        <div className="get-bonus">
            <div className="get-bonus__body">
                <a href="javascript:void(0);" onClick="MainFunction.getBonus();"
                   className="get-bonus__button">
                    Получить <br/>
                    БОНУС
                </a>
            </div>
            <div className="get-bonus__bottom">
                <p className="get-bonus__info">У вас есть 1 бесплатный спин!</p>
            </div>
        </div>
    )
};
const BonusIsNotAvailable = () => {
    return(
        <div className="get-bonus">
            <div className="get-bonus__body">
                <p className="disabled-bonus__button">
                    Получить <br/>
                    БОНУС
                </p>
            </div>
            <div className="get-bonus__bottom">
                <p className="get-bonus__info">Следующий бонус будет доступен
                    через: {/*{{ \Carbon\Carbon::parse($isDailyBonus->created_at)->subDay()->diffForHumans(null, true)}}*/}</p>
                <p id="bonus-timer" className="get-bonus__timer"></p>
            </div>
        </div>
    )
}

export {
    DailyBonusCenter
}