import React from "react";
import defImg from "../../../assets/img/fox.png";

const KingParticipant = ({participant}) => {
    let {img = defImg} = participant;
    return(
        <li className="game__participants-item slick-slide">
            <img src={img} alt="avatar"/>
        </li>
    )
};

export {
    KingParticipant
}