import React, {useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {closeModal, openModal} from "../../../Actions/ModalAction";


const BetModal = () => {
    const dispatch = useDispatch();
    const balance = useSelector(state => state.User.balance);
    const {game_type_id = 1} = useSelector(state => state.Modal.props);
    const [valueBet, setValueBet] = useState(game_type_id?300:10);
    return(
        <div className="modal modal--bet" id="betModal">
            <div className="modal__top">
                <h6 className="modal__title">СДЕЛАТЬ СТАВКУ</h6>
                <a className="modal__close" data-izimodal-close onClick={()=>dispatch(closeModal)}><i className="ic-close"></i></a>
            </div>
            <div class="modal__body">
                <div class="conclusion__balance">
                    <p class="conclusion__balance-text">Текущий баланс:</p>
                    <p class="conclusion__balance-value">{balance} <span>coins</span></p>
                </div>
                <form className="bet__form">
                    <div className="bet__form-wrap">
                        <label className="bet__form-label">COINS</label>
                        <div id="coins" className="bet__form-box">
                            {
                                game_type_id ?
                                    <input type="number"
                                           // onChange="MainFunction.calculate(this, event, 'jackpot');"
                                           onKeyUp="MainFunction.calculate(this, event, 'jackpot');" step="2"
                                           className="bet__form-input cash-coins" min="300" value={valueBet}
                                           onChange={e => {
                                               let nextValue = Number(e.target.value);
                                               if(nextValue >= 300){
                                                   setValueBet(nextValue)
                                               } else {
                                                   setValueBet(300)
                                               }
                                           }}
                                           onKeyPress='validate(event)'/>
                                    :
                                    <input type="number"
                                           // onChange="MainFunction.calculate(this, event, 'jackpot');"
                                           onKeyUp="MainFunction.calculate(this, event);"
                                           className="bet__form-input cash-coins" min="10" max="300" value={valueBet}
                                           onChange={e => {
                                               let nextValue = Number(e.target.value);
                                               if(nextValue >= 10){
                                                   if(nextValue <= 300){
                                                       setValueBet(nextValue)
                                                   } else {
                                                       setValueBet(300);
                                                   }
                                               } else {
                                                   setValueBet(10)
                                               }
                                           }}
                                           onKeyPress='validate(event)'/>
                            }
                            <span className="bet__form-up"></span>
                            <span className="bet__form-down"></span>
                        </div>
                    </div>
                    <span className="bet__form-sum">=</span>
                    <div className="bet__form-wrap bet__form-wrap--gray">
                        <label className="bet__form-label">руб</label>
                        <div id="coins-result" className="bet__form-box count-money">
                            {
                                game_type_id ?
                                    <input type="number" className="bet__form-input" readOnly value={valueBet/10}/>
                                    :
                                    <input type="number" className="bet__form-input" readOnly value={valueBet/10}/>
                            }
                            <span className="bet__form-up"></span>
                            <span className="bet__form-down"></span>
                        </div>
                    </div>
                </form>
                <div className={`warning__balance ${valueBet < balance ? "hide" : ""}`}>
                    <p className="warning__balance-text">* На вашем счете недостаточно средств для ставки</p>
                </div>
            </div>
            <div className="modal__buttons">
                <a href="javascript:void(0);"
                   // onClick="MainFunction.placeBet(this);"

                   className="button button--gradient">СДЕЛАТЬ СТАВКУ</a>
                <a href="#" className="button" data-izimodal-open="#inputModal"
                   onClick={async ()=>{
                       await dispatch(closeModal);
                       setTimeout(()=>{
                           dispatch(openModal("InputPayment"))
                       }, 150)
                   }}
                   data-izimodal-transitionin="fadeInDown">Пополнить</a>
            </div>
        </div>
    )
};


export {
    BetModal
}