import {useDispatch, useSelector} from "react-redux";
import React from "react";
import {openModal} from "../../../Actions/ModalAction";



const CreateGameMobile = () => {
    let isAuth = useSelector(state => state.User.isAuth);
    let dispatch = useDispatch();
    return(
        <div className="create-game mobile-show">
            <div className="create-game__info">
                <p className="create-game__title">Новая игра:</p>
            </div>
            <div className="create-game__body">
                {
                    isAuth?
                        <a href="#javascript:void(0);" data-izimodal-open="#createGame"
                           // onClick="$(this).addClass('hide');"
                           onClick={()=>dispatch(openModal("CreateGameCoinFlip"))}
                           data-izimodal-transitionin="fadeInDown" className="create-game__button">
                            <span>+</span><br/>
                            Создать<br/> игру
                        </a>
                        :
                        <a className="create-game__button" data-izimodal-open="#auth" onClick={()=>dispatch(openModal("Auth"))} data-izimodal-transitionin="fadeInDown">
                            <span>+</span><br/>
                            Создать<br/> игру
                        </a>
                }
            </div>
        </div>
    )
};
const CreateGameBlock = () => {
    let isAuth = useSelector(state => state.User.isAuth);
    let dispatch = useDispatch()
    return(
        <div className="create-game">
            <div className="create-game__info">
                <p className="create-game__title">Новая игра:</p>
            </div>
            <div className="create-game__body">
                {
                    isAuth?
                        <a  data-izimodal-open="#createGame"
                            // onClick="$(this).addClass('hide');"
                            onClick={()=>dispatch(openModal("CreateGameCoinFlip"))}
                           data-izimodal-transitionin="fadeInDown" className="create-game__button">
                            <span>+</span><br/>
                            Создать<br/> игру
                        </a>
                        :
                        <a onClick={()=>dispatch(openModal("Auth"))} className="create-game__button" data-izimodal-open="#auth"
                           data-izimodal-transitionin="fadeInDown">
                            <span>+</span><br/>
                            Создать<br/> игру
                        </a>
                }
            </div>
        </div>
    )
};

export {
    CreateGameMobile,
    CreateGameBlock
}