import React from "react";
import Header from "../../Components/Header/Header";
import {MainWrapper} from "../../Wrapper/MainWrapper";
import {SideBarLeft} from "./SideBarLeft";
import OnlineChat from "../../Components/Chat/Chat";
import {DailyBonusCenter} from "./DailyBonusCenter";



class DailyBonusPage extends React.Component{
    render() {
        return (
            <div>
                <Header/>
                <MainWrapper>
                    <SideBarLeft/>
                    <DailyBonusCenter/>
                    <OnlineChat/>
                </MainWrapper>
            </div>
        );
    }
}

export default DailyBonusPage