import {SWITCH_CHAT_MODE} from "../Actions/OnlineChatAction";

const OnlineChatReducer = (state ={
    isOpen: true,
}, action) => {
    switch (action.type) {

        case SWITCH_CHAT_MODE: return {
            ...state,
            isOpen: !state.isOpen
        };
        default: return state
    }
}

export {
    OnlineChatReducer
}