import {useDispatch, useSelector} from "react-redux";
import noImgBlack from "../../assets/img/icons/user-black.png";
import noImgGray from "../../assets/img/icons/user-gray.png";
import {Link} from "react-router-dom";
import {openModal} from "../../Actions/ModalAction";
import React from "react";

const HeaderRight = ({user = {}, isOpen, handlerOpenMenu}) => {
    const sessionTheme = useSelector(state => state.User.sessionTheme);
    const {name = "nameless", img = sessionTheme?noImgBlack:noImgGray} = user;
    const isAuth = useSelector(state => state.User.isAuth);
    const role_id = useSelector(state => state.User.role_id);
    const dispatch = useDispatch();



    if(isAuth && role_id !== 4) {
        return (
            <div className="header__right user">
                <div className="user__ava">
                    <img src={img} alt="avatar"/>
                </div>
                <div className="user__info">
                <span className="user__level">
                    0 LVL.</span>
                    <span className="user__exp">3000 EXP.</span>
                </div>
                <div className="user__left">
                    <a className="user__open" style={{display: `${!isOpen ? "flex" : "none"}`}}
                       onClick={handlerOpenMenu}>
                        <i className="ic-burger"></i>
                    </a>
                    <a className="user__close" style={{display: `${isOpen ? "flex" : "none"}`}}
                       onClick={handlerOpenMenu}>
                        <i className="ic-close"></i>
                    </a>
                </div>
                <div className="user__dropdown dropdown" style={{display: `${isOpen ? "block" : "none"}`}}>
                    <div className="dropdown__top">
                        <p className="dropdown__level">0 LEVEL.</p>

                        <p className="dropdown__exp">0/ <span>1000 EXP.</span></p>
                        {/*<div className="dropdown__timeline" style="width:{{ color(getExperience(auth()->user())) }}%"></div>*/}
                    </div>
                    <ul className="dropdown__menu">
                        <li><Link to="/profile">Профиль</Link></li>
                        <li><Link to="/profile">История игр</Link></li>
                        <li><Link to="/daily-bonus">Ежедневный бонус</Link></li>
                        <li><Link data-izimodal-open="#outputModal" data-izimodal-transitionin="fadeInDown">Вывести
                            средства <i
                                className="ic-coin"></i></Link></li>
                        <li><Link to="/profile">Промокоды</Link></li>
                        <li><a href="{{ route('logout') }}">Выйти</a></li>
                    </ul>
                </div>
            </div>
        )
    } else {
        if(window.innerWidth < 1024){
            return ""
        }
        return (
            <a className="button chat__bottom-button btn-registration" data-izimodal-open="#auth"
               data-izimodal-transitionin="fadeInDown" onClick={()=>dispatch(openModal("Auth"))}>ВОЙТИ</a>
        )
    }
};

const HeaderRightMobile = ({user = {}, isOpen}) => {
    const sessionTheme = useSelector(state => state.User.sessionTheme);
    const {name = "nameless", img = sessionTheme?noImgBlack:noImgGray} = user;
    return (
        <div className="user--mobile" style={{display: `${isOpen?"flex":"none"}`}}>
            <div className="user__top user__top--active">
                <div className="user__ava">
                    <img src={img} alt="avatar"/>
                </div>
                <div className="user__info">
                    <span className="user__level">0LVL.</span>
                    <span className="user__exp">3000 EXP.</span>
                </div>
                <div className="user__info coins">
                    <span>0<i className="ic-coin"></i></span>
                </div>
                <div className="user__left" style={{display:"none"}}>
                    <a className="user__arrow"></a>
                </div>
            </div>
            <div className="dropdown--mobile dropdown--mobile--active">
                <div className="dropdown__top">
                    <p className="dropdown__level"> 0 LEVEL.</p>
                    <p className="dropdown__exp"> 0 / <span>1000 EXP.</span></p>
                    <div className="dropdown__timeline" >
                        {/*style="width:{{ color(getExperience(auth()->user())) }}%"*/}

                    </div>
                </div>
                <ul className="dropdown__menu">
                    <li><a href="#{{ route('account.profile') }}">Профиль</a>
                    </li>
                    <li><a href="#{{route('account.profile')}}#history">История игр</a>
                    </li>
                    <li><a href="#{{route('account.daily-bonus')}}">Ежедневный бонус</a>
                    </li>
                    <li><a data-izimodal-open="#inputModal" data-izimodal-transitionin="
                        fadeInDown">Пополнить <i class=" ic-coin"></i></a>
                    </li>
                    <li><a data-izimodal-open="#outputModal" data-izimodal-transitionin="
                        fadeInDown">Вывести средства <i class=" ic-coin"></i></a>
                    </li>
                    <li><a href="{{route('account.profile')}}">Промокоды</a>
                    </li>

                    <li><a href="#{{route('logout')}}">Выйти</a>
                    </li>
                </ul>
            </div>
        </div>
    )
};
export {
    HeaderRight,
    HeaderRightMobile
}