import React, {useState} from "react";
import {Link} from "react-router-dom";
import darkLogo from "../../assets/img/logo-dark.png";
import logo from "../../assets/img/logo.png"
import {useDispatch, useSelector} from "react-redux";
import noImgBlack from "../../assets/img/icons/user-black.png"
import noImgGray from "../../assets/img/icons/user-gray.png"
import {openModal} from "../../Actions/ModalAction";
import {HeaderLeft} from "./HeaderLeft";
import {HeaderCenter} from "./HeaderCenter";
import {HeaderRight, HeaderRightMobile} from "./HeaderRight";
import {NavBlockMobile} from "./UI/NavBlock";

class Header extends React.Component {
    state = {
        isOpenMenu: false,
        isOpenUserMenu: false,
        nowIs: this.props.nowIs,
    }
    constructor(props){
        super(props);
        this.handleOpenMenu = this.handleOpenMenu.bind(this);
        this.handleOpenUserMenu = this.handleOpenUserMenu.bind(this);
    }
    componentDidMount() {
        this.setState(prevState => ({...prevState, nowIs: window.location.pathname}))
    }

    handleOpenMenu(){
        this.setState(prevState => ({...prevState, isOpenMenu: !prevState.isOpenMenu}))
    }
    handleOpenUserMenu(){
        this.setState(prevState => ({...prevState, isOpenUserMenu: !prevState.isOpenUserMenu}))
    }


    render() {
        let isOpenMenu = this.state.isOpenMenu;
        let isOpenUserMenu = this.state.isOpenUserMenu;
        let nowIs = this.props.nowIs;
        return (
            <header className="header">
                <HeaderLeft isOpen={isOpenMenu}
                            handlerOpenMenu={this.handleOpenMenu}
                />
                <NavBlockMobile nowIs={nowIs}
                />
                <HeaderCenter isOpen={isOpenMenu}
                              nowIs={nowIs}
                />
                <HeaderRight isOpen={isOpenUserMenu} handlerOpenMenu={this.handleOpenUserMenu}
                />
                <HeaderRightMobile isOpen={isOpenMenu}/>
            </header>
        );
    }
}

export default Header