import React from "react";
import {CrashTopBar} from "./UI/CrashTopBar";
import {ControlPanelMobile} from "./UI/ControlPanel";
import {MainCenterWrapper} from "../../Wrapper/MainCenterWrapper";
import CanvasCrashBlock from "./UI/CanvasCrashBlock";
import {HelpBlockMobile} from "../../Components/HelpBlock";
import {CrashPlayerTable} from "./UI/CrashPlayerTable";
import {BalanceMobile} from "../../Components/Balance";

function CrashCenter() {

    return (
        <MainCenterWrapper>
            <div className="main__center--king-wrap nano">
                <CrashTopBar/>
                <CanvasCrashBlock/>
                <CrashPlayerTable/>
            </div>
            <ControlPanelMobile/>
            <BalanceMobile/>
            <HelpBlockMobile/>
        </MainCenterWrapper>
    )
}


export {
    CrashCenter
}