import React from "react";
import imgVector from "../../assets/img/Vector.png";
import {useDispatch} from "react-redux";
import {closeModal} from "../../Actions/ModalAction";

const CrashHistoryModal = () => {
    let dispatch = useDispatch();
    return(
        <div className="modal modal--help" id="historyCrash">
            <div className="modal__top">
                <h6 className="modal__title"><img src={imgVector}/> История</h6>
                <a href="javascript:void(0);" className="modal__close" onClick={()=>dispatch(closeModal)}>
                    <i className="ic-close">

                    </i>
                </a>
            </div>
            <CrashHistoryModalBody/>
        </div>
    )
};


const CrashHistoryModalBody = ({games = [{},{},{}]}) => {
    return(
        <div className="modal__body">
            <div className="modal-help nano">
                <div className="nano-content rules-of-the-game modal-table">
                    <table className="table">
                        <thead>
                        <tr>
                            <th scope="col">Номер игры</th>
                            <th>Коэффициент</th>
                            <th>Дата</th>
                            <th>Hash</th>
                        </tr>
                        </thead>
                        <tbody>
                            {games.map(game => {
                                return <CrashHistoryModalItem game={game}/>
                            })}
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    )
};
const CrashHistoryModalItem = ({game = {}}) => {
    let {round = 9999, profit = 999, date = "22/22/2222", hash = "91afjsjpaojsikahjnsiodajsf"} = game;
    let color = profit < 3 ? 1 : profit < 4 ? 2 : profit < 50 ? 3 : profit < 100 ? 4 : 5;
    return (
        <tr>
            <th scope="row" className="link-info-game-modal">
                <a href="#javascript:void(0);" data-info="{{$g->profit}}"
                   data-izimodal-open="#historyGame_{{$g->id}}"
                   data-izimodal-transitionin="fadeInDown">{round}</a>
            </th>
            <td className={`text-color-${color}`} scope="row">{profit}x</td>
            <td scope="row">{date}</td>
            <td scope="row">{hash}</td>
        </tr>
    )

}

export {
    CrashHistoryModal
}