import React from "react";
import imgVector from "../../../assets/img/Vector.png"
import {useDispatch} from "react-redux";
import {openModal} from "../../../Actions/ModalAction";

const CrashTopBar = () => {
    let dispatch = useDispatch();
    return(
        <div className="crash-top-bar">
            <a data-izimodal-open="#historyCrash"
               onClick={()=>{dispatch(openModal("CrashHistory"))}}
               data-izimodal-transitionin="fadeInDown"
               className="bank__button">
                <img src={imgVector}/>
                </a>
                <HistoryGameList/>
        </div>
    )
};

const HistoryGameList = ({games = [{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{}]}) => {
    return(
        <ul>
            {games.map(game => {
                return <HistoryGameListItem game={game}/>
            })}
        </ul>
    )
};

const HistoryGameListItem = ({game = {}}) => {
    let {round = 231, hash = "21klfasjuapjsajfhipsdapl", profit = 88} = game;
    let color = profit < 3 ? 1 : profit < 4 ? 2 : profit < 50? 3 : profit < 100 ? 4 : 5;

    return(
        <li className={`color-${color}`}>
            <a href="javascript:void(0);" data-info="{{$g->profit}}"
               data-izimodal-open="#historyGame_{{$g->id}}" data-izimodal-transitionin="fadeInDown">
                {profit}
                <span className="tooltiptext">
                    ROUND: {round}<br/>
                    HASH: {hash}
                </span>
            </a>
        </li>
    )
};
export {
    CrashTopBar
}