import React from 'react';
import { Switch, Route } from 'react-router-dom';
import './App.css';
import "./assets/css/main.min.css";
import "./assets/css/main-dark.min.css";
import JackpotPage from "./Pages/JackpotPage/JackpotPage";
import {useDispatch, useSelector} from "react-redux";
import "../node_modules/izimodal/css/iziModal.min.css";
import CoinFlipPage from "./Pages/CoinflipPage/CoinflipPage";
import KingOfTheHillPage from "./Pages/KingOfTheHillPage/KingOfTheHillPage";
import HelpPage from "./Pages/HelpPage/HelpPage";
import {Error404} from "./Pages/404/404";
import CrashPage from "./Pages/CrashPage/CrashPage";
import ProfilePage from "./Pages/ProfilePage/ProfilePage";
import DailyBonusPage from "./Pages/DailyBonusPage/DailyBonusPage";
import Modal from "react-awesome-modal";
import {CrashHistoryModal} from "./Components/modal/CrashHistoryModal";
import {closeModal} from "./Actions/ModalAction";
import {HelpModal} from "./Components/modal/HelpModal";
import {ChatHelpModal} from "./Components/modal/ChatHelpModal";
import {AuthModal} from "./Components/modal/AuthModal";
import "./assets/css/PopupStyle.css"
import {OutputPaymentModal} from "./Components/modal/OutputPayment";
import {InputPaymentModal} from "./Components/modal/InputPayment";
import {BetModal as BetModalJackPot} from "./Pages/JackpotPage/Modal/BetModal";
import {CreateGame as CreateGameCoinFlip} from "./Pages/CoinflipPage/Modal/CreateGame";
import {BetCoinModalBlue, BetCoinModalOrange} from "./Pages/CoinflipPage/Modal/BetCoin";
import {FirstAwait} from "./Pages/CoinflipPage/Modal/AwaitModal";




const MODALS = {
    "CrashHistory": <CrashHistoryModal/>,
    "HelpModal": <HelpModal/>,
    "ChatHelpModal": <ChatHelpModal/>,
    "Auth": <AuthModal/>,
    "OutputPayment": <OutputPaymentModal/>,
    "InputPayment": <InputPaymentModal/>,
    "BetJackPot": <BetModalJackPot/>,
    "CreateGameCoinFlip": <CreateGameCoinFlip/>,
    "BetCoinOrange": <BetCoinModalOrange/>,
    "BetCoinBlue": <BetCoinModalBlue/>,
    "CoinFlipAwaitFirst": <FirstAwait/>
};

function App() {
    let isVisible = useSelector(state => state.Modal.isVisible);
    let nowIs = useSelector(state => state.Modal.nowIs);
    let dispatch = useDispatch();
    let width = window.innerWidth;

    return (
        <div className="App">
            <Switch>
                <Route exact path='/'>
                    <JackpotPage/>
                </Route>
                <Route exact path='/coinflip'>
                    <CoinFlipPage/>
                </Route>
                <Route exact path='/king-of-the-hill'>
                    <KingOfTheHillPage/>
                </Route>
                <Route exact path='/crash'>
                    <CrashPage/>
                </Route>
                <Route exact path='/help'>
                    <HelpPage/>
                </Route>
                <Route exact path='/profile'>
                    <ProfilePage/>
                </Route>
                <Route exact path='/daily-bonus'>
                    <DailyBonusPage/>
                </Route>
                <Route component={Error404}/>
            </Switch>
            <Modal visible={isVisible} className="lol" width={width < 500 ? "100%" : "65%"} onClickAway={() => dispatch(closeModal)}>
                {MODALS[nowIs]}
            </Modal>
        </div>
    );

}

export default App;
