import React from "react";
import {Link} from "react-router-dom";


export const Error404 = () => {
    return(
        <div className="error">
            <p className="error__number">404</p>
            <h3 className="error__title">Страница не найдена</h3>
            <p className="error__text">К сожалению, веб-страница,
                соответствующая Вашему запросу {window.location.pathname}, была
                удалена или не существует.</p>
            <Link to="/" className="button error__link">НА ГЛАВНУЮ</Link>
        </div>
    )
}