import defImg from "../../../assets/img/fox.png";
import React from "react";

const LastWinnersList = ({winners = [{},{}]}) => {
    return(
        <div className="jackpot-history__wrapper nano" style={{marginTop:"20px"}}>
            <div className="nano-content">
                <ul className="jackpot-history__list last-winners">
                    <div className="jackpot-timer__wrap stats">
                        <p className="history__link history__link--active">ПОСЛЕДНИЕ 10 ПОБЕДИТЕЛЕЙ</p>

                    </div>
                    {winners.map(winner => {
                        return <LastWinnersListItem winner={winner}/>
                    })}
                </ul>
            </div>
        </div>
    )
};
const LastWinnersListItem = ({winner = {}}) => {
    let {color = "#3de", name = "nameless", img = defImg, cash = 3000} = winner;
    return(
        <li className="jackpot-history__item history-item history-item--"
            style={{borderLeft: `2px solid ${color}`}}>
            <div className="history-item__left">
                <img src={img} alt="avatar" className="history-item__avatar"/>
                <div className="history-item__wrap">
                    <p className="history-item__name">{name}</p>
                </div>
            </div>
            <div className="history-item__right">
                <p className="history-item__value">{cash} <span>COINS</span></p>
                <p className="history-item__range">
                    <i className="ic-game-history">
                    </i> 3000
                </p>
            </div>
        </li>
    )
};


export {
    LastWinnersList
}