import React from "react";
import Header from "../../Components/Header/Header";
import {MainWrapper} from "../../Wrapper/MainWrapper";
import {SideBarLeft} from "./SideBarLeft";
import {KingOfTheHillCenter} from "./KingOfTheHillCenter";
import OnlineChat from "../../Components/Chat/Chat";
import {HelpBlockMobile} from "../../Components/HelpBlock";


class KingOfTheHillPage extends React.Component {
    render() {
        return (
            <div>
                <Header nowIs={"/king-of-the-hill"}/>
                <MainWrapper>
                    <SideBarLeft/>
                    <KingOfTheHillCenter/>
                    <OnlineChat/>
                </MainWrapper>
                <HelpBlockMobile/>

            </div>
        );
    }
}

export default KingOfTheHillPage