import React from "react";
import {useDispatch, useSelector} from "react-redux";
import {openModal} from "../Actions/ModalAction";


function Balance() {
    let isAuth = useSelector(state => state.User.isAuth);
    let dispatch = useDispatch();
    if(isAuth){
        return (
            <div className="balance">
                <p className="balance__title">Баланс:</p>
                <p className="balance__value">
                    0
                    <span>COINS</span></p>
                <div className="balance__action">
                    <a className="button balance__input" data-izimodal-open="#inputModal"
                       data-izimodal-transitionin="fadeInDown" onClick={()=> dispatch(openModal("InputPayment"))}>ПОПОЛНИТЬ</a>
                    <a className="button button--gray balance__output @if(checkSessionTheme()) button--dark @endif"
                       data-izimodal-open="#outputModal" data-izimodal-transitionin="fadeInDown" onClick={()=>dispatch(openModal('OutputPayment'))}>ВЫВЕСТИ</a>
                </div>
            </div>
        );
    } else {
        return ""
    }
}
function BalanceMobile() {
    let isAuth = useSelector(state => state.User.isAuth);
    let dispatch = useDispatch();
    if(isAuth){
        return (
            <div className="balance mobile-show">
                <p className="balance__title">Баланс:</p>
                <p className="balance__value">
                    0
                    <span>COINS</span></p>
                <div className="balance__action">
                    <a className="button balance__input" data-izimodal-open="#inputModal"
                       data-izimodal-transitionin="fadeInDown" onClick={()=> dispatch(openModal("InputPayment"))}>ПОПОЛНИТЬ</a>
                    <a className="button button--gray balance__output @if(checkSessionTheme()) button--dark @endif"
                       data-izimodal-open="#outputModal" data-izimodal-transitionin="fadeInDown" onClick={()=>dispatch(openModal('OutputPayment'))}>ВЫВЕСТИ</a>
                </div>
            </div>
        );
    } else {
        return ""
    }
}

///@if(isset($isMobile)) mobile-show @endif style for mobile need to connect to class

export default Balance

export {
    BalanceMobile
}