import React, {useState} from "react";


const HistoryWrap = () => {
    let [nowIs, setNowIs] = useState(0);
    return(
        <div id="history" className="history">
            <div className="history__top">
                <ul className="history__links">
                    <li className={`history__link ${!nowIs?"history__link--active":""}`}
                        onClick={()=>setNowIs(0)}
                    >ИСТОРИЯ БАЛАНСА </li>
                     /
                    <li className={`history__link ${nowIs?"history__link--active":""}`}
                        onClick={()=>setNowIs(1)}> ИСТОРИЯ ИГР</li>
                </ul>
                <a className="history__help hide" data-izimodal-open="#outputRules"
                   data-izimodal-transitionin="fadeInDown"><i className="ic-rules"></i>Правила вывода</a>
            </div>
            {
                !nowIs?
                    <HistoryBalance/>
                    :
                    <HistoryGames/>
            }
        </div>
    )
}
const HistoryBalance = () => {
    return(
        <div id="historyBodyOne" className="history__body history__body--active nano">
            <HistoryBalanceList/>
        </div>
    )
};
const HistoryBalanceList = ({offers = [{payment_id: 1},{payment_id: 2},{payment_id: 3}]}) => {
    return(
        <ul className="history__list">
            {offers.map(offer => {
                return <HistoryBalanceListItem offer={offer}/>
            })}
        </ul>
    )
};
const HistoryBalanceListItem = ({offer}) => {
    let {payment_id = 1} = offer;
    switch (payment_id) {
        case 1:{
            let {price = 999, name = "nameless", date = "22/22/2222"} = offer;
            return (
                <li className="history__item hist-balance hist-balance--green">
                    <div className="hist-balance__left">
                        <p className="hist-balance__sum"><i className="ic-money-green"></i>{price} Руб.</p>
                    </div>
                    <div className="hist-balance__right">
                        <p className="hist-balance__source">{name}</p>

                        <p className="hist-balance__data">{date}</p>
                    </div>
                </li>
            );
        }
        case 2:{
            let {price = 999, name = "nameless", date = "22/22/2222"} = offer;
            return (
                <li className="history__item hist-balance hist-balance--red">
                    <div className="hist-balance__left">
                        <p className="hist-balance__sum"><i className="ic-money-red"></i>{price} Руб.</p>
                    </div>
                    <div className="hist-balance__right">
                        <p className="hist-balance__source">{name}</p>
                        <p className="hist-balance__data">{date}</p>
                    </div>
                </li>
            );
        }
        default:{
            let {price = 999, name = "nameless", date = "22/22/2222"} = offer;
            return (
                <li className="history__item hist-balance hist-balance--yellow">
                    <div className="hist-balance__left">
                        <p className="hist-balance__sum">
                            <i className="ic-money-yellow">++
                            </i>{price} Руб.
                        </p>
                    </div>
                    <div className="hist-balance__right">
                        <p className="hist-balance__source--user">
                            <i className="ic-user-black"></i> Игрок <span>{name}</span> пополнил
                            баланс
                        </p>
                        <p className="hist-balance__data">{date}</p>
                    </div>
                </li>
            )
        }
    }
};

const HistoryGames = () => {
    return(
        <div id="historyBodyTwo" className="history__body history__body--active nano">
            <div className="nano-content">
                <HistoryGamesList/>
            </div>
        </div>
    )
};
const HistoryGamesList = ({offers = [{payment_id: 3},{payment_id: 2, winner: "nameless", account: "nameless"},{payment_id: 2, winner: "nameless", account: "nameless1"}]}) => {
    return(
        <ul className="history__list">
            {offers.map(offer => {
                return <HistoryGamesListItem offer={offer}/>
            })}
        </ul>
    )
};

const HistoryGamesListItem = ({offer = {}}) => {
    let {payment_id = 0} = offer;
    if(payment_id === 3) {
        let {price = 999, name = "nameless", date="22/22/2222"} = offer;
        return(
            <li className="history__item hist-balance hist-balance--yellow">
                <div className="hist-balance__left">
                    <p className="hist-balance__sum"><i className="ic-money-yellow"></i>{price} Руб.</p>
                </div>
                <div className="hist-balance__right">

                    <p className="hist-balance__source--user"><i className="ic-user-black"></i> Игрок <span>{name}</span> пополнил баланс</p>
                    <p className="hist-balance__data">{date}</p>
                </div>
            </li>
        )
    } else {
        let {winner = "", account = ""} = offer;
        if(winner === account){
            let {cash = 9999, nameGame = "Jackpot", date = "22/22/2222"} = offer;
            return (
                <li className="history__item hist-balance hist-balance--game hist-balance--green">
                    <div className="hist-balance__left">
                        <p className="hist-balance__sum">
                            <i className="ic-money-green">
                            </i>{cash} coins</p>
                        <p className="hist-balance__game">{nameGame}</p>
                    </div>
                    <div className="hist-balance__right">
                        <p className="hist-balance__data">{date}</p>
                    </div>
                </li>
            )
        } else {
            let {cash = 9999, nameGame = "Jackpot", date = "22/22/2222"} = offer;
            return (
                <li className="history__item hist-balance hist-balance--game hist-balance--red">
                    <div className="hist-balance__left">
                        <p className="hist-balance__sum">
                            <i className="ic-money-red"></i>{cash} coins</p>
                        <p className="hist-balance__game">{nameGame}</p>
                    </div>
                    <div className="hist-balance__right">
                        <p className="hist-balance__data">{date}</p>
                    </div>
                </li>
            )
        }
    }
}

export {
    HistoryWrap
}