import React, {useState} from "react";
import {MainCenterWrapper} from "../../Wrapper/MainCenterWrapper";
import {ProfileBlockMobile} from "./UI/ProfileBlock";
import {HelpBlockMobile} from "../../Components/HelpBlock";
import {Referral} from "./UI/Referral";
import {HistoryWrap} from "./UI/History";



function ProfileCenter() {

    return(
        <MainCenterWrapper>
            <ProfileBlockMobile/>
            <Referral/>
            <HistoryWrap/>
            <HelpBlockMobile/>
        </MainCenterWrapper>
    )
}



export {
    ProfileCenter
}