import React from "react";
import {useSelector} from "react-redux";
import {MainCenterWrapper} from "../../../Wrapper/MainCenterWrapper";


const ProfileBlock = () => {
    let avatar = useSelector(state => state.User.avatar);
    let name = useSelector(state => state.User.name);
    return(
        <div className="profile">
            <div className="profile__avatar">
                <img src={avatar} alt="avatar"/>
            </div>
            <p className="profile__name">{name}</p>
        </div>
    )
};
const ProfileBlockMobile = () => {
    let avatar = useSelector(state => state.User.avatar);
    let name = useSelector(state => state.User.name);
    return(
        <div className="profile mobile-show">
            <div className="profile__avatar">
                <img src={avatar} alt="avatar"/>
            </div>
            <p className="profile__name">{name}</p>
        </div>
    )
};


export {
    ProfileBlock,
    ProfileBlockMobile
}