import React from "react";
import defImg from "../../../assets/img/fox.png";
import {useDispatch, useSelector} from "react-redux";
import {openModal} from "../../../Actions/ModalAction";


function HistoryListGame({games = [{}, {}]}) {
    return(
        <ul className="coin-game-history">
            {games.map(game => {
                return <HistoryListGameItem game={game}/>
            })}
        </ul>
    )
}
const HistoryListGameItem = ({game = {}}) => {
    return (
        <li className="coin-game-history__item">
            <ListGameItemLeft game={game}/>
            <ListGameItemRight game={game}/>
        </li>
    )
};
const ListGameItemLeft = ({game = {}}) => {
    let {winner = {count: 0, name: "nameless", img: defImg, bet: 999},looser = { count: 1, name: "nameless", img: defImg, bet: 999}} = game;
    return(
        <div className="coin-game-history__left">
            <div className="coin-game-history__box">
                {/*@if($game->winner_ticket <= 50)*/}
                {winner.count ?
                    <i className="ic-coin-orange"></i>
                    :
                    <i className="ic-coin-blue"></i>
                }

                <img src={winner.img} alt="avatar" className="coin-game-history__winner"/>
                <img src={looser.img} alt="avatar" className="coin-game-history__loser"/>
            </div>
            <p className="coin-game-history__name">
                {/*{{ $game->winner->name.' '.$game->winner->last_name }}*/}
                {winner.name}
            </p>
            <div className="coin-game-history__wrap">
                <p className="coin-game-history__text">выиграл </p>
                <p className="coin-game-history__value">
                    {/*{{ $game->participants()->sum('cash') }}*/}
                    {winner.bet + looser.bet}
                    <span>COINS</span></p>
            </div>
        </div>
    )
};
const ListGameItemRight = ({game = {}}) => {
    let {game_id = 0} = game;
    let isAuth = useSelector(state => state.User.isAuth);
    let role_id = useSelector(state => state.User.role_id);
    let dispatch = useDispatch();
    return(
        <div className="coin-game-history__right">
            <a href="#" className="coin-game-history__number">Classic #{game_id}</a>
            {
                isAuth && role_id !== 4 ?
                    <a className="coin-game-history__see" href="#waitPlayer" data-izimodal-open="#waitPlayer"
                       data-izimodal-transitionin="fadeInDown" onClick="MainFunction.showGame(this, {{ $game->id }});">
                        <i className="ic-eye"></i>
                    </a>
                    :
                    <a className="coin-game-history__see" href="#waitPlayer" data-izimodal-open="#waitPlayer"
                       onClick={() => dispatch(openModal("Auth"))}
                       data-izimodal-transitionin="fadeInDown">
                        <i className="ic-eye"></i>
                    </a>

            }
        </div>
    )
};

export {
    HistoryListGame
}