import React from "react";
import defImg from "../../../assets/img/fox.png";

const CrashPlayerTable = () => {
    let ubets = [];
    if(ubets.length === 0){
        return (
            <div className="crash-player-table" id="app">
                <NoActivePlayers/>
            </div>
        )
    } else {
        return(
            <div className="crash-player-table" id="app">
                <CrashPlayerList/>
            </div>
        )
    }
};

const CrashPlayerList = ({ubets = [{},{}]}) => {
    return (
        <>
            <div className="crash-table-body-name name">
                <div className="crash-body-title">Пользователь</div>
                <div className="crash-body-title">Ставка</div>
                <div className="crash-body-title">Коэффициент</div>
                <div className="crash-body-title">Выигрыш</div>
            </div>
            <div className="block-state-crash">
                {ubets.map(bet => {
                    return <CrashPlayerListItem bet={bet}/>
                })}
            </div>
        </>
    )
};

const CrashPlayerListItem = ({bet = {}}) => {
    let {name = "nameless", price = 9999, img = defImg, cash = 999, profit = 399} = bet;
    return(
        <div className="result-table crash-table-body user-{{$bet['user']->id}}" data-cashout="{{$bet['bet']->number}}"
             data-bet="{{$bet['bet']->price}}" data-user="{{$bet['user']->id}}">
            <div className="crash-body-title img">
                <img src={img} alt="avatar" width="20" height="20"/> <div className="name-user">{name}</div>
            </div>
            <div className="crash-body-title bet">{price}</div>
            <div className="crash-body-title cash">{cash}</div>
            <div className="crash-body-title profit">{profit}</div>
        </div>
    )
}

const NoActivePlayers = () => {
    return(
        <div style={{textAlign: "center"}}>
            Нет активных игроков
        </div>
    )
};


export {
    CrashPlayerTable
}