import React from "react";
import defImg from "../../../assets/img/fox.png";


const ClassicWinner = ({game = {}}) => {
    let {winCash = 9999} = game;
    return(
        <div className="winner classic" >
            <h5 className="winner__title">ПОБЕДИТЕЛЬ</h5>
            <div className="winner__wrap">
                <WinnerLeft/>
                <WinnerCenter game={game}/>
                <WinnerRight winCash = {winCash}/>
            </div>
        </div>
    )
};

const WinnerLeft = () => {
    return(
        <div className="winner__left winner-circle">
            <p className="winner-circle__text">СТАВКА ПОБЕДИТЕЛЯ</p>
            <span className="winner-circle__numbers">25%</span>
        </div>
    )
};
const WinnerCenter = ({winner = {}, game = {}}) => {
    let {img = defImg, name = "nameless", cashValue = 9999} = winner;
    let {hash = "D14A028C2A3A2BC9476102BB288234C41" } = game;
    return(
        <div className="winner__center">
            <div className="winner__center-top">
                <div className="winner__avatar">
                    <img src={img} alt="avatar"/>
                </div>
                <div className="winner__box ">
                    <p className="winner__name">{name}</p>
                    <p className="winner__value ">{cashValue}<span>COINS</span></p>
                    {/*hide style for winner__value*/}
                </div>
            </div>
            <p className="winner__hash">Round number:
                {/*hide style for winner_hash*/}
                <a href="#">{hash}</a></p>
        </div>
    )
};
const WinnerRight = ({winCash}) => {
    return(
        <div className="winner__right winner-circle">
            <p className="winner-circle__text">СУММА ВЫИГРЫША</p>
            <span className="winner-circle__numbers">
                <i className="ic-dollar">
                </i>
                {winCash}
            </span>
        </div>
    )
};


export {
    ClassicWinner
}