import React from "react";
import defImg from "../../../assets/img/fox.png"

function GameHistory ({games = [{},{}]}) {
    return(
        <ul className="game-history">
            {games.map(game => {
                return <GameHistoryItem game={game}/>
            })}
        </ul>
    )
}

const GameHistoryItem = ({game = {}}) => {
    let {winnerImg = defImg,winnerName = "nameless", totalCash = 9999, game_id = 999999, gameEnd = "5 day ago"} = game;
    return(
        <li className="game-history__item">
            <div className="game-history__left">
                <div className="game-history__avatar">
                    <img src={winnerImg} alt="avatar"/>
                </div>
                <p className="game-history__name">
                    {/*{{$game->winner->name}} */}
                    {winnerName}
                </p>
                <span className="game-history__text">выиграл</span>
                <p className="game-history__result">
                    {/*{{$game->participants->sum('cash')}} */}
                    {totalCash}
                    <span>COINS</span></p>
            </div>
            <div className="game-history__right">
                <p className="game-history__title">
                    {game_id}
                    {/*{{$game->type->name}} #{{$game->id}}*/}
                </p>
                <p className="game-history__time">
                    <i className="ic-clock"></i>
                    {gameEnd}
                    {/*{{ \Carbon\Carbon::parse($game->end_game_at)->diffForHumans()}}*/}
                </p>
            </div>
        </li>
    )
};

export {
    GameHistory
}