import React, {useState} from "react";
import {ParticipantsWrapper} from "./UI/Participants";
import {ChooseWinner} from "./ChooseWinner";
import defImg from "../../assets/img/fox.png";
import {JackpotHistory} from "./JackpotHistory";
import {MainCenterWrapper} from "../../Wrapper/MainCenterWrapper";
import {JackpotTimer} from "./UI/JackpotTimer";
import {BankMobile} from "./UI/Bank";
import {RoomsTypeBlockMobile} from "./UI/RoomTypeBlock";
import {LastWinnersList} from "./UI/LastWinners";
import {HelpBlockMobile} from "../../Components/HelpBlock";
import {BalanceMobile} from "../../Components/Balance";

function JackpotCenter({game = {players: [{},{},{}]}}) {
    return (
        <MainCenterWrapper>
            <RoomsTypeBlockMobile/>
            <ParticipantsWrapper/>
            <ChooseWinner game={game}/>
            <GameThirdStep/>
            <JackpotTimer/>
            <JackpotHistory/>
            <BankMobile/>
            <LastWinnersList/>
            <BalanceMobile/>
            <HelpBlockMobile/>
        </MainCenterWrapper>
    )
}
const GameThirdStep = () => {
    return (
        <div className="winner winner-step-3 ">
            {/*
            @if($game && strtotime($game->end_game_at) < strtotime($time) && strtotime($game->animation_at) - 5 <= strtotime($time)
			&& strtotime($game->animation_at) >= strtotime($time)
			&& $game->winner_ticket)  @else hide @endif
                */}
            <h5 className="winner__title">ПОБЕДИТЕЛЬ</h5>
            <div className="winner__wrap">
                <div className="winner__left winner-circle">
                    <p className="winner-circle__text">ШАНС ВЫИГРЫША</p>
                    <span
                        className="winner-circle__numbers winner-percent">
                            {/*{{$result ? $result['percent'] : ''}}%*/} 0%
                        </span>
                </div>
                <div className="winner__center">
                    <div className="winner__center-top">
                        <div className="winner__avatar">
                            <img src={defImg} alt="avatar"/>
                        </div>
                        <div className="winner__box">
                            <p className="winner__name">
                                {/*{{$result ? $result['name'] : ''}}*/}Test
                            </p>
                            <p className="winner__value">
                                {/*{{$result ? $result['bank'] : ''}}*/} 3000
                                <span>COINS</span></p>
                        </div>
                    </div>
                    <p className="winner__hash">
                        Round number: <a target="_blank" href="{{ $result ? $result['linkHash'] : '' }}">
                        2
                        {/*{{$result ? $result['hash'] : ''}}*/}
                    </a>
                    </p>
                </div>
                <div className="winner__right winner-circle">
                    <p className="winner-circle__text">СЧАСЛИВЫЙ БИЛЕТ</p>
                    <span className="winner-circle__numbers winner-number">
                        <i className="ic-dollar"></i>
                        1
                        {/*{{$result ? $result['winnerTicket'] : ''}}*/}
                        </span>
                </div>
            </div>
        </div>
    )
};


export {
    JackpotCenter
}