import React from "react";
import defImg from "../../../assets/img/fox.png"

function ParticipantsWrapper ({game = {players: []}}){
    return(
        <div className="wrap-first-step ">
            <div
                className="winners">
                <a href="#" className="winners__game">
                    {/*{{$gameType->name}} @if($game) #{{$game->id}} @endif*/}
                    classic
                </a>
                <h5 className="winners__title">Игроки</h5>
                <div className="winners__slider">
                    <Participants game={game}/>
                </div>
            </div>
        </div>
    )
};

const Participants = ({game = {players: [],isPlaying: false}}) => {
    if(game.isPlaying){
        return (
            <>
                {game.players.map(player => {
                    return <ParticipantsWinner player={player}/>
                })}
            </>
        )
    }else {
        return (
            <>
                <div className="winners__slider-item winners__slider-item--">
                    <img src={defImg} alt="avatar" className="winners__slider-avatar"/>
                    <span className="winners__slider-percent"></span>
                </div>
                <div className="winners__slider-item winners__slider-item--">
                    <img src={defImg} alt="avatar" className="winners__slider-avatar"/>
                    <span className="winners__slider-percent"></span>
                </div>
            </>
        )
    }
}
const ParticipantsWinner = ({player = {}, bank = 3000}) => {
    let {img = defImg, color = "#333", cash = 3000}= player;
    return (
        <div className="winners__slider-item winners__slider-item--"
             style={{borderBottom: `3px solid ${color}`}}>
            <img src={img} alt="avatar" className="winners__slider-avatar"/>
                <span className="winners__slider-percent">{Math.round(cash * 100 / bank)}%</span>
        </div>
    )
}

export {
    ParticipantsWrapper
}