export const OPEN_MODAL = "OPEN_MODAL";
export const CLOSE_MODAL = "CLOSE_MODAL";


const openModal = (modalName, props={}) => dispatch => {
    dispatch({
        type: OPEN_MODAL,
        modalName,
        props
    })
};


const closeModal = dispatch => {
    dispatch({
        type: CLOSE_MODAL,
    })
};


export {
    openModal,
    closeModal
}