import React from "react";
import {useSelector} from "react-redux";
import {CreateGameMobile} from "./UI/CreateGame";
import {HistoryStatistics} from "./UI/HistoryStatistics";
import defImg from "../../assets/img/fox.png";
import {CoinFlipWrapGameItem} from "./UI/GameBeforeStart";
import {AnimationGame} from "./UI/AnimationGame";
import {HelpBlockMobile} from "../../Components/HelpBlock";
import {HistoryListGame} from "./UI/HistoryListGame";

function CoinFlipCenter (){
    let isOpen = useSelector(state => state.Chat.isOpen);
    return(
        <div className={`main__center main__center--king ${isOpen?"" : "main__center--close"}`}>
            <CreateGameMobile/>
            <div className="main__center--king-wrap nano">
                <div className="nano-content">
                    <HistoryStatistics/>
                    <CoinHint/>
                    <CoinFlipWrapGame/>
                    <AnimationGame/>
                    <HistoryListGame/>
                </div>

            </div>
            <HelpBlockMobile/>
            <PlayerList/>
        </div>
    )
};





const CoinFlipWrapGame = ({games = [{},{participants:[{count:1}]}]}) => {
    return(
        <div className="wrap-coinflip">
            {games.map(game => {
                return <CoinFlipWrapGameItem game={game}/>
            })}
        </div>
    )
}



const CoinHint = () => {
    return(
        <ul className="coin-hint">
            <li className="coin-hint__text">Игрок</li>
            <li className="coin-hint__text">Ставка</li>
            <li className="coin-hint__text">Наблюдение</li>
            <li className="coin-hint__text">Ставка</li>
            <li className="coin-hint__text">Игрок</li>
        </ul>
    )
}
const PlayerList = () => {
    return(
        <ul className="hide">
            <li>
                <a data-izimodal-open="#createGame" data-izimodal-transitionin="fadeInDown">Окно 1. Создание игры</a>
            </li>
            <li className="hide">
                <a data-izimodal-open="#waitPlayer" data-izimodal-transitionin="fadeInDown">Окно 2, ожидание второго
                    игрока или
                    зрителя</a>
            </li>
            <li>
                <a data-izimodal-open="#waitPlayer2" data-izimodal-transitionin="fadeInDown">Окно 3 , когда
                    присоединился второй
                    игрок</a>
            </li>
            <li>
                <a data-izimodal-open="#waitPlayer3" data-izimodal-transitionin="fadeInDown">Окно 4 ,добавляем следующий
                    глаз</a>
            </li>
            <li>
                <a data-izimodal-open="#waitPlayer4" data-izimodal-transitionin="fadeInDown">Окно 5 , игра началась</a>
            </li>
            <li>
                <a data-izimodal-open="#waitPlayer5" data-izimodal-transitionin="fadeInDown">Окно 6. Выигрыш</a>
            </li>
            <li>
                <a data-izimodal-open="#waitPlayer6" data-izimodal-transitionin="fadeInDown">Окно 6. Выигрыш от первого
                    игрока</a>
            </li>
            <li>
                <a data-izimodal-open="#waitPlayer7" data-izimodal-transitionin="fadeInDown">Окно 6. Выигрыш от второго
                    игрока</a>
            </li>
            <li>
                <a data-izimodal-open="#waitPlayer8" data-izimodal-transitionin="fadeInDown">Окно 6 Окно для третьего
                    лица</a>
            </li>
        </ul>
    )
}

export {
    CoinFlipCenter
}