import React, {useState} from "react";

const RoomsTypeBlockMobile = () => {
    const [gameTypeID, setGameTypeID] = useState(0);
    return(
        <div className="rooms mobile-show">
            <p className="rooms__title">Выбор комнаты:</p>
            <ul className="rooms__wrap">
                <li className={`rooms__link ${!gameTypeID? "rooms__link--active":""}`} data-game-id="1"
                    onClick={()=>setGameTypeID(0)}>CLASSIC
                </li>
                <li className={`rooms__link ${gameTypeID? "rooms__link--active":""}`} data-game-id="2"
                    onClick={()=>setGameTypeID(1)}>SMALL
                </li>
            </ul>
            <div id="roomClassic" className="rooms__box rooms__box--active">
                <p className="rooms__text">Минимальная ставка от 300 coins</p>
            </div>
            <div id="roomSmall" className="rooms__box">
                <p className="rooms__text">Максимальная ставка до 300 coins</p>
            </div>
        </div>
    )
};

const RoomsTypeBlock = () => {
    const [gameTypeID, setGameTypeID] = useState(0);
    return (
        <div className="rooms">
            <p className="rooms__title">Выбор комнаты:</p>
            <ul className="rooms__wrap">
                <li className={`rooms__link ${!gameTypeID ? 'rooms__link--active' : ""}`} data-game-id="1"
                    onClick={() => setGameTypeID(0)}>
                    CLASSIC
                </li>
                <li className={`rooms__link ${gameTypeID ? 'rooms__link--active' : ""}`} data-game-id="2"
                    onClick={() => setGameTypeID(1)}>
                    SMALL
                </li>
            </ul>
            <div id="roomClassic" className={`rooms__box ${!gameTypeID ? "rooms__box--active" : ""}`}>
                <p className="rooms__text">Минимальная ставка от 300 coins</p>
            </div>
            <div id="roomSmall" className={`rooms__box ${gameTypeID ? "rooms__box--active" : ""}`}>
                <p className="rooms__text">Максимальная ставка до 300 coins</p>
            </div>
        </div>
    )
};

export {
    RoomsTypeBlock,
    RoomsTypeBlockMobile
}