import React from "react";
import {useDispatch} from "react-redux";
import {closeModal} from "../../Actions/ModalAction";


const HelpModal = () => {
    let dispatch = useDispatch();
    return(
        <div className="modal modal--help" id="helpModal">
            <div className="modal__top">
                <h6 className="modal__title">Правила игры Classic</h6>
                <a className="modal__close" data-izimodal-close onClick={()=>dispatch(closeModal)}><i className="ic-close"></i></a>
            </div>
            <div className="modal__body">
                <div className="modal-help nano">
                    <div className="nano-content rules-of-the-game">
                        <p><b>Правила jackpot:</b><br/>Игра имеющая общий банк, в который игроки делают производят взносы
                            в виде "Coins" за 1 раунд можно внести не более 3 взносов. По окончанию таймера,
                            разыгрывается общий банк между участниками игры. При внесении " Coins" в игру, игрок
                            получает виртуальные билеты, один из которых становится выигрышным, за 1 внесенный "Coins"
                            вы получите 1 билет, за 100 внесенных "Coins" вы получите 100 билетов в банке. Победитель
                            определяется с помощью прокрутки ролла с номерами билетов, победитель забирает весь банк
                            раунда. Данная игра имеет 2 режима Classic с внесением минимальных взносов от 300 coins и
                            Small c внесением взносов до 300 коинс.</p>
                        <p><b>Правила Coinflip:</b><br/>Это игра, где происходит сражение двух игроков между собой,
                            ставки вносятся в виде "Coins" у обоих имеются одинаковые шансы на победу 50/50. т.е если
                            один игрок создал игру на 100 "Coins" то присоединиться к игре можно только сделав
                            аналогичный взнос 100 "Coins" не больше и не меньше. По окончанию таймера разыгрывается банк
                            между двумя участниками игры с помощью прокрутки ролла. При внесении "Coins" в игру, игрок
                            получает виртуальные билеты, один из которых будет выигрышным, за 1 внесенный "Coins" вы
                            получите 1 билет, за 100 внесенных "Coins" вы получите 100 билетов в банке.</p>
                        <p><b>Правила King of the Hill:</b><br/>Игра аукционного типа в которой после взноса каждого
                            участника включается таймер с отчетом 20 секунд. Каждый последующий взнос должен быть больше
                            предыдущего. Победителем объявляется участник сделавший последний взнос и по окончанию 20
                            секунд которого никто так и не перебил взносом более крупной суммы. Победитель забирает все
                            сливки игры состоящие из суммы всех взносов участников данного раунда Classic - установлен
                            фиксированный взнос с каждым последующим увеличением шага на 10 coins Senyor - установлен
                            произвольный взнос с последующим увеличением шага на любую сумму.</p>
                    </div>
                </div>
            </div>
        </div>
    )
};

export {
    HelpModal
}