import React from "react";
import {useDispatch} from "react-redux";
import {openModal} from "../Actions/ModalAction";

const HelpBlock = () => {
    let dispatch = useDispatch();
    return (
        <div className="help">
            <p className="help__title">Помощь</p>
            <a href="#" className="help__link"
               onClick={()=>dispatch(openModal("HelpModal"))}
               data-izimodal-open="#helpModal"
               data-izimodal-transitionin="fadeInDown"
            >
                <i className="ic-rules">
                </i>
                Правила игры
            </a>
        </div>
    )
};
const HelpBlockMobile = () => {
    let dispatch = useDispatch();
    return(
        <div className="help mobile-show">
            <p className="help__title">Помощь</p>
            <a className="help__link"
               onClick={()=>dispatch(openModal("HelpModal"))}
               data-izimodal-open="#helpModal"
               data-izimodal-transitionin="fadeInDown"
            >
                <i className="ic-rules"></i>
                Правила игры
            </a>
        </div>
    )
}
export {
    HelpBlock,
    HelpBlockMobile
}