import {CLOSE_MODAL, OPEN_MODAL} from "../Actions/ModalAction";

const ModalReducer = (state = {
    nowIs: "",
    isVisible: false,
    props: {}
},action) => {
    switch (action.type) {
        case OPEN_MODAL: return {
            ...state,
            nowIs: action.modalName,
            isVisible: true,
            props: action.props
        };
        case CLOSE_MODAL: return {
            ...state,
            nowIs: "",
            isVisible: false,
            props: {}
        };
        default: return state
    }
};

export {
    ModalReducer
}