import React, {useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {closeModal} from "../../Actions/ModalAction";
import YMoneyImg from "../../assets/img/yandex.png";
import VisaMasterImg from "../../assets/img/mv.png";

const OutputPaymentModal = () => {
    let dispatch = useDispatch();
    const balance = useSelector(state => state.User.balance);
    const [valueCashOut, setValueCashOut] = useState(0);
    return (
        <div className="modal modal--conclusion" id="outputModal">
            <input type="hidden" name="balance" value="{{ getBalance(auth()->user()) }}"/>
            <div className="modal__top">
                <h6 className="modal__title">Вывод средств</h6>
                <a className="modal__close" data-izimodal-close onClick={() => dispatch(closeModal)}>
                    <i className="ic-close">
                    </i>
                </a>
            </div>
            <div className="modal__body">
                <div className="conclusion__balance">
                    <p className="conclusion__balance-text">Текущий баланс:</p>
                    <p className="conclusion__balance-value">{balance} <span>coins</span></p>
                </div>
                <div className="output-box">
                    <div className="output-box__top">
                        <p className="output-box__text">Сумма</p>
                        <input type="number" className="output-box__field" value={valueCashOut} onChange={(e)=>{
                            let nextValue = Number(e.target.value);
                            if(nextValue < balance && nextValue < 15000){
                                setValueCashOut(nextValue);
                            } else {
                                if(balance < 15000){
                                    setValueCashOut(balance)
                                } else {
                                    setValueCashOut(15000)
                                }
                            }
                        }}/>
                        <p className="output-box__hint">
                            Максимальная сумма за раз: 15000р <br/>
                            Минимальная сумма: 200р
                        </p>
                    </div>
                    <div className="output-box__bottom">
                        <p className="output-box__text">Реквизиты</p>
                        <p className="output-box__requisites">+799379992</p>
                    </div>
                    <div className="warning__balance hide">
                        <p className="warning__balance-text">* Неккоректные данные</p>
                    </div>
                </div>
                <div className="conclusion__where">
                    <p className="conclusion__where-title">КУДА ВЫВЕСТИ</p>
                    <PaymentList/>
                    <div id="fieldRequisites" style={{display: "block"}} className="output-requisites">
                        <p className="output-requisites__text">Реквизиты:</p>
                        <input type="text" name="requisites" className="output-requisites__input"
                               placeholder="Введите номер"/>
                    </div>
                </div>
                <div className="modal__buttons">
                    <a href="javascript:void(0);" onClick="Payment.withdrawalOfFunds(this);"
                       className="button button--border button--wide">Вывод средств</a>
                </div>
            </div>
        </div>
    )
};
const PaymentList = () => {
    const [nowIs, setNowIs] = useState(1);
    return(
        <ul className="conclusion__where-list">
            <li>
                <label className="output-radio">
                    <input type="radio" checked={nowIs === 1} value="1" name="outputType" onChange={()=>setNowIs(1)}/>
                    <span className="output-radio__box" onClick={()=>setNowIs(1)}>
                                    <i className="ic-qiwi"></i>
                                </span>
                </label>
            </li>
            <li>
                <label className="output-radio">
                    <input type="radio" checked={nowIs === 2} name="outputType" value="2" onChange={()=>setNowIs(2)}/>
                    <span className="output-radio__box" onClick={()=>setNowIs(2)}>
                                    <i className="ic-sber"></i>
                                </span>
                </label>
            </li>
            <li>
                <label className="output-radio">
                    <input type="radio" checked={nowIs === 3} name="outputType" value="3" onChange={()=>setNowIs(3)}/>
                    <span className="output-radio__box" onClick={()=>setNowIs(3)}>
                                    <img src={YMoneyImg} alt="yandex money" style={{width: "120px", marginTop: "10px"}}/>
                                </span>
                </label>
            </li>
            <li>
                <label className="output-radio">
                    <input type="radio" checked={nowIs === 4} name="outputType" value="4" onChange={()=>setNowIs(4)}/>
                    <span className="output-radio__box" onClick={()=>setNowIs(4)}>
                                    <img src={VisaMasterImg} alt="visa and mastercard payment" style={{width: "120px", marginTop: "10px"}}/>
                                </span>
                </label>
            </li>
        </ul>
    )
};

export {
    OutputPaymentModal
}