import React from "react";
import {useDispatch, useSelector} from "react-redux";
import {openModal} from "../../../Actions/ModalAction";



const GameTimer = ({status_id = 2,timer = 25, participants = [{},{}]}) => {
    let isAuth = useSelector(state => state.User.isAuth);
    let role_id = useSelector(state => state.User.role_id);
    let dispatch = useDispatch();
    return(
        <div className="game__time">
            <p className="game__time-text classic-timer
                                    @if(!$gameClassic || !$gameClassic->end_game_at || $gameClassic->end_game_at < now() || $gameClassic->participants->groupBy('account_id')->count() < 2) @endif">
                Осталось:
                <span className="game__time-value">00:<span
                    className="game__time-counter">
                    {status_id > 1 ? timer : "20"}
                    {/*{{($gameClassic->status_id > 1) ? strtotime($gameClassic->end_game_at) - strtotime($timer) : '20'}}*/}
                </span></span>
            </p>
            {
                isAuth && role_id !== 4 ?
                    <a className="game__time-link button button--gradient" id="bedBet"
                       onClick="MainFunction.createGameKing('classic');">ВНЕСТИ
                        <span> {participants.length? participants.length * 10 + 10 : 10} </span>COINS
                    </a>
                    :
                    <a className="game__time-link button button--gradient" id="bedBet" data-izimodal-open="#auth"
                       onClick={()=>dispatch(openModal("Auth"))}
                       data-izimodal-transitionin="fadeInDown">ВНЕСТИ
                        <span> {participants.length? participants.length * 10 + 10 : 10} </span>COINS

                        COINS
                    </a>
            }
        </div>
    )
};

export {
    GameTimer
}