import React from "react";
import {Link} from "react-router-dom";


const NavBlock = ({nowIs}) => {
    return(
        <nav className="nav">
            <ul className="nav__list">
                <li className="nav__item">
                    <Link to="/" className={`nav__link ${nowIs === "/" ? "nav__link--active" : ""}`}>
                        <i className="ic-nav1">
                        </i>JACKPOT
                    </Link>
                </li>
                <li className="nav__item">
                    <Link to="/coinflip" className={`nav__link ${nowIs === "/coinflip" ? "nav__link--active" : ""}`}>
                        <i className="ic-nav2">
                        </i>COINFLIP
                    </Link>
                </li>
                <li className="nav__item">
                    <Link to="/king-of-the-hill" className={`nav__link ${nowIs === "/king-of-the-hill" ? "nav__link--active" : ""}`}>
                        <i className="ic-nav3">
                        </i>KING OF THE HILL
                    </Link>

                </li>
                <li className="nav__item">
                    <Link to="/crash" className={`nav__link ${nowIs === "/crash" ? "nav__link--active" : ""}`}>
                        <i className="ic-nav5">
                        </i>CRASH
                    </Link>

                </li>
                <li className="nav__item">
                    <Link to="/help" className={`nav__link ${nowIs === "/help" ? "nav__link--active" : ""}`}>
                        <i className="ic-nav4">
                        </i>HELP
                    </Link>
                </li>
            </ul>
        </nav>
    )
};
const NavBlockMobile = ({nowIs}) => {
    return (
        <nav className="nav mobile-show">
            <ul className="nav__list">
                <li className="nav__item">
                    <Link to="/" className={`nav__link ${nowIs === "/" ? "nav__link--active" : ""}`}>
                            <i className="ic-nav1">
                            </i>JACKPOT
                    </Link>
                </li>
                <li className="nav__item">
                    <Link to="/coinflip" className={`nav__link ${nowIs === "/coinflip" ? "nav__link--active" : ""}`}>
                            <i className="ic-nav2">
                            </i>COINFLIP
                    </Link>
                </li>
                <li className="nav__item">
                    <Link to="/king-of-the-hill" className={`nav__link ${nowIs === "/king-of-the-hill" ? "nav__link--active" : ""}`}>
                            <i className="ic-nav3">
                            </i>KING OF THE HILL
                    </Link>
                </li>
                <li className="nav__item">

                    <Link to="/crash" className={`nav__link ${nowIs === "/crash" ? "nav__link--active" : ""}`}>
                            <i className="ic-nav3 ic-nav5">
                            </i>CRASH
                    </Link>
                </li>

            </ul>
        </nav>
    )
};

export {
    NavBlock,
    NavBlockMobile
}