import React from "react";
import imgVector2 from "../../../assets/img/Vector2.png";
import "material-icons";

class CanvasCrashBlock extends React.Component {
    state = {
        countOfPerson: 0,
        totalBets: 0,
        totalWinBets: 0
    }
    render() {
        let {countOfPerson, totalBets, totalWinBets} = this.state;
        return (
            <div className="canvas-crash-block">
                <canvas id="crashGraphic" height="450">

                </canvas>
                <div className="flex-container-round">
                    <div className="crash-round">
                        Общие ставки: <span className="person-dp-sp">
                        <i className="material-icons material-icons--person">
                            person
                        </i>
                        <span id="users_count_bet" style={{marginLeft: 0}}>
                        {countOfPerson}
                        </span>
                        <span>
                            <img src={imgVector2}/> <span id="users_total_bet" style={{marginLeft: 0}}> {totalBets}
                                </span>
                            </span>
                        </span>
                    </div>
                    <div className="crash-round sp">
                        Общие выигрыши: <span>
                        <img src={imgVector2}/>
                        <span id="users_count_bet_win">
                            {totalWinBets}
                        </span>
                    </span>
                    </div>
                </div>
            </div>
        );
    }
}

export default CanvasCrashBlock