import React from "react";


const GameBankMobile = ({totalCash = 9999}) => {
    return(
        <div className="game__bank game__bank--mobile">
            <p className="game__bank-title">В БАНКЕ:</p>
            <span className="game__bank-value">{totalCash}</span>
            <span className="game__bank-text">COINS</span>
        </div>
    )
};
const GameBank = ({totalCash = 9999}) => {
    return(
        <div className="game__bank">
            <p className="game__bank-title">В БАНКЕ:</p>
            <span className="game__bank-value">{totalCash}</span>
            <span className="game__bank-text">COINS</span>
        </div>
    )
}
export {
    GameBankMobile,
    GameBank
}