import React from "react";
import {useDispatch, useSelector} from "react-redux";
import defImg from "../../../assets/img/fox.png";
import {openModal} from "../../../Actions/ModalAction";

const CoinFlipWrapGameItem = ({game = {}}) => {
    let {participants = [{count: 2}]} = game;
    if(participants[0].count === 2 ){
        return(
            <div className="coin-game" data-game-id="{{ $game->id }}">
                <CoinGameLeft/>
                <CoinGameEye/>
                <CoinGameRight/>
            </div>
        )
    } else {
        return (
            <div className="coin-game" data-game-id="{{ $game->id }}">
                <CoinGameLeftRev/>
                <CoinGameEyeRev/>
                <CoinGameRightRev/>
            </div>
        )
    }

}

const CoinGameEye = () => {
    let isAuth = useSelector(state => state.User.isAuth);
    let role_id = useSelector(state => state.User.role_id);
    let dispatch = useDispatch();

    return(
        <div className="coin-game__eye">
            {
                isAuth && role_id != 4 ?

                    <a href="javascript:void(0);" data-izimodal-open="#waitPlayer" onClick="MainFunction.showGame(this);"
                       data-izimodal-transitionin="fadeInDown" style={{display:"block", width:"100%", height:"100%"}}>
                        <i className="ic-eye"></i>
                    </a>
                    :
                    <a data-izimodal-open="#auth" data-izimodal-transitionin="fadeInDown" onClick={() => dispatch(openModal("Auth"))}
                       style={{display:"block", width:"100%", height:"100%"}}>
                        <i className="ic-eye"></i>
                    </a>
            }
        </div>
    )
};
const CoinGameLeft = ({participant = {}}) => {
    let {count = 2} = participant;
    if(count != 1){
        return (
            <div className="coin-game__action coin-game__action--orange">
                <a href="javascript:void(0);" onClick="MainFunction.checkAuthUser(this);"
                   className="coin-game__button">ИГРАТЬ</a>
            </div>
        )
    } else {
        let {name = "nameless", img = defImg, cashValue = 3999} = participant;
        return(
            <div className="coin-game__left">
                <div className="coin-game__wrap2">
                    <div className="coin-game__box">
                        <img src={img}
                             alt="avatar" className="coin-game__avatar"/>
                        <i className="ic-coin-orange"></i>
                    </div>
                    <p className="coin-game__name">
                        {name}
                        {/*{{$game->participants->sortByDesc('created_at')->first()->account->name}}*/}
                    </p>
                </div>
                <div className="coin-game__wrap">
                    <p className="coin-game__text">сделал ставку </p>
                    <p className="coin-game__value">
                        {cashValue}
                        {/*{{$game->participants->sortByDesc('created_at')->first()->cash}}*/}
                        <span>COINS</span></p>
                </div>
            </div>
        )
    }
};

const CoinGameRight = ({participant = {}}) => {
    let {name = "nameless", img = defImg, cashValue = 3999} = participant;

    return(
        <div className="coin-game__right">
            <div className="coin-game__wrap">
                <p className="coin-game__value">
                    {cashValue}
                    {/*{{$game->participants->first()->cash}} */}
                    <span>COINS</span></p>
                <p className="coin-game__text">сделал ставку </p>
            </div>
            <div className="coin-game__wrap2">
                <p className="coin-game__name">
                    {name}
                    {/*{{$game->participants->first()->account->name}}*/}
                </p>
                <div className="coin-game__box">
                    <img src={img} alt="avatar"
                         className="coin-game__avatar"/>
                    <i className="ic-coin-blue"></i>
                </div>
            </div>
        </div>
    )
};
const CoinGameLeftRev = ({participant = {}}) => {
    let {name = "nameless", img = defImg, cashValue = 3999} = participant;
    return(
        <div className="coin-game__left">
            <div className="coin-game__wrap2">
                <div className="coin-game__box">
                    <img src={img} alt="avatar"
                         className="coin-game__avatar"/>
                    <i className="ic-coin-orange"></i>
                </div>
                <p className="coin-game__name">
                    {name}
                    {/*{{$game->participants->first()->account->name}}*/}
                </p>
            </div>
            <div className="coin-game__wrap">
                <p className="coin-game__text">сделал ставку </p>
                <p className="coin-game__value">
                    {cashValue}
                    {/*{{$game->participants->first()->cash}}*/}
                    <span>COINS</span></p>
            </div>
        </div>
    )
}
const CoinGameRightRev = ({participant = {}}) => {
    let {count = 1} = participant;
    if(count !== 2) {
        return(
            <div className="coin-game__action coin-game__action--blue">
                <a href="javascript:void(0);" onClick="MainFunction.checkAuthUser(this);"
                   className="coin-game__button">ИГРАТЬ</a>
            </div>
        )
    } else {
        let {name = "nameless", img = defImg, cashValue = 3999} = participant;
        return(
            <div className="coin-game__right">
                <div className="coin-game__wrap">
                    <p className="coin-game__value">{cashValue}
                        <span>COINS</span></p>
                    <p className="coin-game__text">сделал ставку </p>
                </div>
                <div className="coin-game__wrap2">
                    <p className="coin-game__name">{name}</p>
                    <div className="coin-game__box">
                        <img src={img}
                             alt="avatar" className="coin-game__avatar"/>
                        <i className="ic-coin-blue"></i>
                    </div>
                </div>
            </div>
        )
    }
}
const CoinGameEyeRev = () => {
    let dispatch = useDispatch();

    let isAuth = useSelector(state => state.User.isAuth);
    let role_id = useSelector(state => state.User.role_id);
    return(
        <div className="coin-game__eye">
            {
                isAuth && role_id !== 4 ?
                    <a href="#waitPlayer" data-izimodal-open="#waitPlayer" data-izimodal-transitionin="fadeInDown"
                       onClick="MainFunction.showGame(this);"
                       style={{display:"block", width:"100%", height:"100%"}}>
                        <i className="ic-eye">

                        </i>
                    </a>
                    :
                    <a data-izimodal-open="#auth" data-izimodal-transitionin="fadeInDown" onClick={() => dispatch(openModal("Auth"))}
                       style={{display:"block", width:"100%", height:"100%"}}>
                        <i className="ic-eye">

                        </i>
                    </a>
            }
        </div>
    )
}

export {
    CoinFlipWrapGameItem
}