import React from "react";
import {useDispatch} from "react-redux";
import {closeModal} from "../../Actions/ModalAction";


const ChatHelpModal = () => {
    let dispatch = useDispatch();
    return (
        <div class="modal modal--help" id="chatRules">
            <div class="modal__top">
                <h6 class="modal__title">Правила чата</h6>
                <a class="modal__close" onClick={() => dispatch(closeModal)} data-izimodal-close><i
                    class="ic-close"></i></a>
            </div>
            <div class="modal__body">
                <div class="modal-chat nano">
                    <div class="nano-content rules-chat">
                        <ol>
                            <li>Условия действия правил чата
                                <ol>
                                    <li>Пользователи, заходя в чат, принимают на себя добровольные обязательства
                                        беспрекословно соблюдать нижеперечисленные правил.
                                    </li>
                                    <li>Незнание правил не освобождает от ответственности за их нарушения, убедительно
                                        рекомендуем с ними ознакомиться.
                                    </li>
                                </ol>
                            </li>
                            <li>В чате разрешается:
                                <ol>
                                    <li>Общаться, делиться информацией и обсуждать любые темы, а также совершать любые
                                        другие действия не нарушающие п3.
                                    </li>
                                    <li>Предлагать улучшения работы чата, жаловаться на плохую работу чата.</li>
                                </ol>
                            </li>
                            <li>В чате категорически запрещены:
                                <ol>
                                    <li>Мошенничество и запрещенные программы.
                                        <ol>
                                            <li>Мошенничество.</li>
                                            - сообщения о любых финансовых сделках, продажах, обмене, дарении, передачи
                                            аккаунта во владения другого участника;

                                            <br/>- создание фейков;

                                            <br/>- категорически запрещено выдавать себя за представителя Администрации
                                            проекта.

                                            <br/>Запрещенные программы, распространение, использование.

                                            <br/>- сообщения, содержащие в себе распространение, спрос и обсуждение
                                            запрещенных программ в чате.
                                        </ol>
                                    </li>
                                    <li>Мат и завуалированный мат.
                                        <br/>Открытый мат (слова, фразы) и завуалированный мат (эвфемизмы, недописанные
                                        слова, сокращения, слова с заменой одной или более букв, так и слова, со
                                        звездочками и т.п.), ненормативная лексика на любых языках, в любой кодировке, а
                                        так же слова оскорбительного характера - запрещены во всех их проявлениях!
                                        <ol>
                                            <li>Открытый мат.</li>
                                            <li>Завуалированный мат и слова оскорбительного характера</li>
                                        </ol>
                                    </li>
                                    <li>Дискриминация по расовому, национальному, религиозному или иному
                                        признаку/Националистические, расовые лозунги и высказывания.
                                        <ol>
                                            <li>Сообщения, призывающие к каким-либо действиям против других
                                                национальностей.
                                            </li>
                                            <li>Сообщения, в которых говорится о превосходстве одной нации или народа
                                                над другими.
                                            </li>
                                            <li>Сообщения, ведущие к межнациональной, расовой ненависти и розни, а также
                                                ведущие к провоцированию конфликтов на религиозной почве.
                                            </li>
                                        </ol>
                                    </li>
                                    <li>Пропаганда насилия, оружия, наркотиков, алкоголя и табакокурения и т.д.
                                        <br/>сообщения, содержащие в себе пропаганду насилия, оружия, рекламу и открытые
                                        обсуждения любых наркотических средств, а так же пропаганда алкогольных напитков
                                        и табачных изделий, сообщения сексуально-порнографического характера, в том
                                        числе текстовые смайлы анатомического характера в чате запрещены.
                                    </li>
                                    <li>Запрещается прямо и косвенно оскорблять других пользователей в чате
                                        (использование литературных разговорных слов при оскорблении или хамстве
                                        пользователей чата), хамить пользователям и администрации чата, устраивать
                                        разборки/выяснения отношений.
                                    </li>
                                    <li>Спам, флуд, реклама.</li>
                                    <li>Флуд
                                        <ol>
                                            <br/>- отправка большого количества однотипных сообщений в течение 3 минут;
                                            <br/>- использование более 3 (трех) любых символов или отдельных букв,
                                            повторение более 3 (трех) одинаковых слов подряд в одном сообщении;
                                            <br/>- отправка нескольких сообщений, которые могут быть объединены в одно,
                                            написанных по одному слову или по одной букве.
                                            <li>Спам/ Реклама/Ссылки:
                                                <br/>- коммерческая и некоммерческая реклама.
                                            </li>
                                            <li>Троллинг, флейм, распространение личной информации.
                                                <br/>Троллинг - сообщения провокационного характера, написанные с целью
                                                вызвать конфликты, споры, взаимные оскорбления.
                                                <br/>Троллинг/обсуждение действий модераторов:
                                                <br/>- множественные вопросы Модераторам, задаваемые за небольшой период
                                                времени, с целью затруднить работу модераторов;
                                                <br/>- сообщения, содержащие в себе обсуждения действий Модератора и/или
                                                Администрации проекта или несогласие с ними.
                                                <br/>Спам – реклама какого либо ресурса в явной форме.
                                            </li>
                                        </ol>
                                    </li>
                                    <li>Разглашение личной информации:
                                        <br/>- сообщения, разглашающие любую личную информацию участника, Модератора,
                                        Администрации проекта без его согласия.
                                    </li>
                                    <li>Угрозы и оскорбления.
                                        <ol>
                                            <li>Угрозы:
                                                <br/>- сообщения, содержащие в себе прямые или косвенные угрозы расправы
                                                в реальной жизни, вымогательство и шантаж.
                                            </li>
                                            <li>Оскорбления:
                                                <br/>- оскорбление в любой форме, унижение человеческого достоинства и
                                                слова
                                            </li>
                                            <li>Оскорбление модератора:
                                                <br/>- оскорбление Модератора, представителей Администрации или компании
                                                <br/>- угрозы расправы или угрозы снятия с модераторского поста.
                                            </li>
                                        </ol>
                                    </li>
                                    <li>Заниматься попрошайничеством.</li>
                                </ol>
                            </li>
                            <li>В чате не рекомендуется
                                <ol>
                                    <li>Злоупотреблять «Caps Lock»ом.</li>
                                    <li>Злоупотреблять смайлами (например, отправлять 5 или более сообщений подряд,
                                        содержащих только смайлы).
                                    </li>
                                    <li>Отправлять объемные цитаты, в том числе порезанные на части в основной чат. При
                                        необходимости Вы можете указать ссылку на источник информации.
                                    </li>
                                    <li>Наш чат - русскоязычный, следовательно, официальный язык общения – русский, не
                                        рекомендуются использовать при общении в нашем чате другие языки, допускаются
                                        исключения в некоторых случаях: написание текста на транслите, или международном
                                        языке (английский язык).
                                    </li>
                                    <li>Игнорировать предупреждения просьбы модераторов чата.</li>
                                </ol>
                            </li>
                        </ol>
                    </div>
                </div>
            </div>
        </div>
    )
};

export {
    ChatHelpModal
}