import React from "react";
import {MainCenterWrapper} from "../../Wrapper/MainCenterWrapper";
import {HelpBlockMobile} from "../../Components/HelpBlock";
import {AnswersList} from "./UI/AnswersList";


function HelpCenter(){
    return(
        <MainCenterWrapper>
            <div className="starting">
                <h2 className="starting__title">РАЗДЕЛ ПОМОЩИ</h2>
                <p className="starting__text">Мы постораемся помочь вам с проблемами, но сперва
                    посмотрите уже готовые решения ниже..</p>
            </div>
            <AnswersList/>
            <HelpBlockMobile/>
        </MainCenterWrapper>
    )
}

export {
    HelpCenter
}