import React from "react";
import {KingParticipant} from "./KingParticipants";
import defImg from "../../../assets/img/fox.png";
import {GameBank, GameBankMobile} from "./GameBank";
import {GameTimer} from "./GameTime";

function ClassicGame ({game = {}}){
    let {status = true, participants = [{},{}]} = game;
    return(
        <div className="game game-classic">
            <ClassicGameLeft status={status} participants={participants}/>
            <ClassicGameCenter status={status} participants={participants}/>
            <GameBankMobile/>
            <ClassicGameRight/>
        </div>
    )
}

const ClassicGameLeft = ({status, participants}) => {

    return(
        <div className="game__left">
            <p className="game__title">Classic #</p>
            <ul className="game__participants game__participants_classic">
                {
                    status ?
                        <>
                            {participants.reverse().map(participant => {
                                return <KingParticipant participant={participant}/>
                            })}
                        </>
                        :
                        ""
                }
            </ul>
        </div>
    )
};

const ClassicGameCenter = ({status, participants}) => {
    let {firstPlayerImg = defImg} = participants[0];
    return(
        <div className="game__center">
            <div className="game__winner">
                {
                    status && participants.length > 0?
                        <img src={firstPlayerImg} alt="winner"
                             className="game__winner-img"/>
                             :
                        <img src={defImg} alt="winner" className="game__winner-img"/>
                }
            </div>
        </div>
    )
};

const ClassicGameRight = () => {
    return(
        <div className="game__right">
            <GameBank/>
            <GameTimer/>
        </div>
    )
}
export {
    ClassicGame
}