import React, {useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {openModal} from "../../../Actions/ModalAction";

const betSetHandler = (value,currentValue, setState, balance) => {
    if(value + currentValue < balance && value + currentValue > 0){
        setState(value + currentValue);
    } else {
        if(value + currentValue <= 0) {
            setState(0)
        } else {
            setState(balance)
        }
    }
};




const ControlPanel = () => {
    let isAuth = useSelector(state => state.User.isAuth);
    let role_id = useSelector(state => state.User.role_id);
    let dispatch = useDispatch();
    return(
        <div className="controls-panel-crash">
            <form method="POST" id="bet_form">
                <BetBlock/>
                <CashOutBlock/>
                {isAuth && role_id != 4 ?
                    <button className="button button--gradient" id="dp_bet" type="submit" disabled="true">
                        Играть
                    </button>
                    :
                    <a className="button button--gradient bank__button" data-izimodal-open="#auth"
                       onClick={()=>dispatch(openModal("Auth"))}
                       data-izimodal-transitionin="fadeInDown">
                        Играть
                    </a>
                }
            </form>
        </div>
    )
};
const ControlPanelMobile = () => {
    let isAuth = useSelector(state => state.User.isAuth);
    let role_id = useSelector(state => state.User.role_id);
    let dispatch = useDispatch();
    return(
        <div className="controls-panel-crash mobile-show">
            <form method="POST" id="bet_form">
                <BetBlock/>
                <CashOutBlock/>
                {isAuth && role_id != 4 ?
                    <button className="button button--gradient" id="dp_bet" type="submit" disabled="true">Сделать
                        ставку
                    </button>
                    :
                    <a className="button button--gradient bank__button" data-izimodal-open="#auth"
                       onClick={()=>dispatch(openModal("Auth"))}
                       data-izimodal-transitionin="fadeInDown">
                        СДЕЛАТЬ
                    </a>
                }
            </form>
        </div>
    )
};

const BetBlock = () => {
    let balance = useSelector(state => state.User.balance);
    let [currentBet, setCurrentBet] = useState(0);
    return(
        <div className="block-bet">
            <div className="crash-bet-title">Введите Вашу ставку:</div>

            <div className="block-bet-val">
                <div className="clear-bet-name clear-bet" onClick={()=> setCurrentBet(0)}>Сброс</div>
                <div className="clear-bet-val {{ $btn_bets }}">
                    <ul>
                        <li data-action="+10"
                            onClick={()=>{
                                betSetHandler(10, currentBet, setCurrentBet,balance);
                            }}
                        >+10</li>
                        <li data-action="+50"
                            onClick={()=>{
                                betSetHandler(50, currentBet, setCurrentBet,balance);
                            }}
                        >+50</li>
                        <li data-action="+100"
                            onClick={()=>{
                                betSetHandler(100, currentBet, setCurrentBet,balance);
                            }}
                        >+100</li>
                        <li data-action="+500"
                            onClick={()=>{
                                betSetHandler(500, currentBet, setCurrentBet,balance);
                            }}
                        >+500</li>
                        <li data-action="+1000"
                            onClick={()=>{
                                betSetHandler(1000, currentBet, setCurrentBet,balance);
                            }}
                        >+1000</li>
                        <li data-action="half"
                            onClick={()=>{
                                betSetHandler(Math.floor(currentBet/2), currentBet, setCurrentBet,balance);
                            }}
                        >1/2</li>
                        <li data-action="X2"
                            onClick={()=>{
                                betSetHandler(currentBet, currentBet, setCurrentBet,balance);
                            }}
                        >2X</li>
                        <li data-action="%10"
                            onClick={()=>{
                                betSetHandler(Math.floor((currentBet * 10)/100), currentBet, setCurrentBet,balance);
                            }}
                        >10%</li>
                        <li data-action="%25"
                            onClick={()=>{
                                betSetHandler(Math.floor((currentBet * 25)/100), currentBet, setCurrentBet,balance);
                            }}
                        >25%</li>
                        <li data-action="%50"
                            onClick={()=>{
                                betSetHandler(Math.floor((currentBet * 50)/100), currentBet, setCurrentBet,balance);
                            }}
                        >50%</li>
                        <li data-action="max"
                            onClick={()=>{
                                betSetHandler(balance, currentBet, setCurrentBet,balance);
                            }}
                        >MAX</li>
                    </ul>
                </div>
            </div>
            {/*{!!$bl !!}*/}
            <input type="number"
                   name="bet"
                   id="bet"
                   value={currentBet}
                   autoComplete="off"
                   placeholder="Ваша ставка"
                   step="any"
                   min="0"
                   onChange={(e)=>{
                       let value = e.target.value;
                       if(value < balance){
                           setCurrentBet( value);
                       } else {
                           setCurrentBet(balance);
                       }
                   }}
            />
        </div>
    )
};

const CashOutBlock = () => {
    return(
        <div className="block-bet top">
            <div className="crash-bet-title">Автовывод:</div>

            <div className="block-bet-val">
                <div className="clear-bet-name" id="clear_cashout">Сброс</div>
                <div className="clear-bet-val cashout">
                    <ul>
                        <li data-action="1">MIN</li>
                        <li data-action="+.1">+0.1</li>
                        <li data-action="+.5">+0.5</li>
                        <li data-action="+1.00">+1</li>
                        <li data-action="-.1">-0.1</li>
                        <li data-action="-.5">-0.5</li>
                        <li data-action="-1.00">-1</li>
                        <li data-action="X2">2X</li>
                        <li data-action="X5">5X</li>
                        <li data-action="X10">10X</li>
                    </ul>
                </div>
            </div>

            <input type="number" name="cashout" id="cashout" placeholder="Автовывод" value="0"
                   autoComplete="off" step="0.01"
                   min="0" onKeyPress="doubleValidate(this)"
            />
        </div>
    )
};


export {
    ControlPanel,
    ControlPanelMobile
}