import React from "react";
import {useSelector} from "react-redux";
import {ClassicWinner} from "./UI/ClassicWinner";
import {ClassicGame} from "./UI/ClassicGame";
import {GameHistory} from "./UI/GameHistory";



function KingOfTheHillCenter() {
    let isOpen = useSelector(state => state.Chat.isOpen);
    return(
        <div className={`main__center main__center--king ${isOpen?"" : "main__center--close"}`}>
            <div className="main__center--king-wrap nano">
                <div className="nano-content">
                    <ClassicWinner/>
                    <ClassicGame/>
                    <GameHistory/>
                </div>
            </div>
        </div>
    )
}

export {
    KingOfTheHillCenter
}